"use client"

import { useCallback, useEffect, useRef, useState } from "react"
import { createClient } from "@supabase/supabase-js"

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
)

const MY_ID = "1264833597240643605"
const PARTNER_ID = "1264833597240643605"

type View = "Home" | "Messages" | "Voicemails" | "Gallery"
type Actor = "me" | "partner"
type Post = { id: string; text: string; created_at: string }
type Voicemail = { id: string; url: string; created_at: string }
type DirectMessage = { id: string; sender: Actor; receiver: Actor; body: string; created_at: string }
type Album = { id: string; name: string; created_at: string }
type AlbumImage = { id: string; album_id: string; url: string; created_at: string }
type Lanyard = {
  discord_user: { username: string; global_name?: string | null; avatar?: string | null }
  discord_status: string
  activities?: Array<{ type: number; name: string }>
  spotify?: { album_art_url: string; song: string; artist: string } | null
}

function StatusCard({
  label,
  id,
  user,
}: {
  label: string
  id: string
  user: Lanyard | null
}) {
  if (!user?.discord_user) return null
  const statusColor =
    user.discord_status === "online"
      ? "bg-green-500"
      : user.discord_status === "dnd"
      ? "bg-red-500"
      : user.discord_status === "idle"
      ? "bg-yellow-500"
      : "bg-gray-400"
  const avatar = user.discord_user.avatar
    ? `https://cdn.discordapp.com/avatars/${id}/${user.discord_user.avatar}.png`
    : "https://cdn.discordapp.com/embed/avatars/0.png"
  return (
    <div className="p-4 rounded-3xl bg-white/70 border border-white shadow-sm">
      <div className="flex items-center gap-3">
        <div className="relative">
          <img src={avatar} alt="avatar" className="w-11 h-11 rounded-2xl object-cover border-2 border-white" />
          <span className={`absolute -right-1 -bottom-1 w-4 h-4 rounded-full border-2 border-white ${statusColor}`} />
        </div>
        <div>
          <p className="text-[10px] uppercase tracking-[0.2em] font-black text-[#cc8292]/50">{label}</p>
          <p className="font-black italic text-[#cc8292] leading-none">
            {user.discord_user.global_name || user.discord_user.username}
          </p>
          {user.spotify && (
            <p className="text-[10px] mt-1 uppercase tracking-widest text-[#1DB954]">
              {user.spotify.song} · {user.spotify.artist}
            </p>
          )}
        </div>
      </div>
    </div>
  )
}

export default function MegaCapsule() {
  const [view, setView] = useState<View>("Home")
  const [posts, setPosts] = useState<Post[]>([])
  const [voicemails, setVoicemails] = useState<Voicemail[]>([])
  const [directMessages, setDirectMessages] = useState<DirectMessage[]>([])
  const [albums, setAlbums] = useState<Album[]>([])
  const [albumImages, setAlbumImages] = useState<AlbumImage[]>([])

  const [postInput, setPostInput] = useState("")
  const [dmInput, setDmInput] = useState("")
  const [newAlbumName, setNewAlbumName] = useState("")
  const [sender, setSender] = useState<Actor>("me")
  const [renameDrafts, setRenameDrafts] = useState<Record<string, string>>({})
  const [selectedAlbumId, setSelectedAlbumId] = useState<string | null>(null)

  const [myStatus, setMyStatus] = useState<Lanyard | null>(null)
  const [partnerStatus, setPartnerStatus] = useState<Lanyard | null>(null)
  const [timeSince, setTimeSince] = useState("")
  const [isRecording, setIsRecording] = useState(false)
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const [uploadingAlbumId, setUploadingAlbumId] = useState<string | null>(null)
  const [error, setError] = useState("")

  const mediaRecorder = useRef<MediaRecorder | null>(null)
  const mediaStream = useRef<MediaStream | null>(null)
  const audioChunks = useRef<Blob[]>([])

  const loadBase = useCallback(async () => {
    const [p, v, d, a] = await Promise.all([
      supabase.from("posts").select("*").order("created_at", { ascending: false }),
      supabase.from("voicemails").select("*").order("created_at", { ascending: false }),
      supabase.from("direct_messages").select("*").order("created_at", { ascending: true }),
      supabase.from("albums").select("*").order("created_at", { ascending: true }),
    ])
    const firstError = p.error ?? v.error ?? d.error ?? a.error
    if (firstError) return setError(firstError.message)
    setPosts((p.data ?? []) as Post[])
    setVoicemails((v.data ?? []) as Voicemail[])
    setDirectMessages((d.data ?? []) as DirectMessage[])
    const nextAlbums = (a.data ?? []) as Album[]
    setAlbums(nextAlbums)
    setRenameDrafts((prev) => {
      const next = { ...prev }
      for (const album of nextAlbums) if (!next[album.id]) next[album.id] = album.name
      return next
    })
    setSelectedAlbumId((prev) => (prev && nextAlbums.some((a2) => a2.id === prev) ? prev : nextAlbums[0]?.id ?? null))
  }, [])

  const loadImages = useCallback(async (albumId: string | null) => {
    if (!albumId) return setAlbumImages([])
    const { data, error: e } = await supabase
      .from("album_images")
      .select("*")
      .eq("album_id", albumId)
      .order("created_at", { ascending: false })
    if (e) return setError(e.message)
    setAlbumImages((data ?? []) as AlbumImage[])
  }, [])

  useEffect(() => {
    const t = setTimeout(() => void loadBase(), 0)
    return () => clearTimeout(t)
  }, [loadBase])
  useEffect(() => {
    const i = setInterval(() => void loadBase(), 8000)
    return () => clearInterval(i)
  }, [loadBase])
  useEffect(() => {
    const t = setTimeout(() => void loadImages(selectedAlbumId), 0)
    return () => clearTimeout(t)
  }, [selectedAlbumId, loadImages])

  useEffect(() => {
    const start = new Date("January 28, 2026 00:00:00").getTime()
    const i = setInterval(() => {
      const d = Date.now() - start
      setTimeSince(
        `${Math.floor(d / 86400000)}d ${Math.floor((d % 86400000) / 3600000)}h ${Math.floor((d % 3600000) / 60000)}m ${Math.floor((d % 60000) / 1000)}s`
      )
    }, 1000)
    return () => clearInterval(i)
  }, [])

  useEffect(() => {
    const fetchStatus = async () => {
      try {
        const [a, b] = await Promise.all([
          fetch(`https://api.lanyard.rest/v1/users/${MY_ID}`).then((r) => r.json() as Promise<{ success: boolean; data: Lanyard }>),
          fetch(`https://api.lanyard.rest/v1/users/${PARTNER_ID}`).then((r) => r.json() as Promise<{ success: boolean; data: Lanyard }>),
        ])
        if (a.success) setMyStatus(a.data)
        if (b.success) setPartnerStatus(b.data)
      } catch {}
    }
    void fetchStatus()
    const i = setInterval(() => void fetchStatus(), 15000)
    return () => clearInterval(i)
  }, [])

  useEffect(() => () => mediaStream.current?.getTracks().forEach((t) => t.stop()), [])

  const sendPost = async () => {
    const text = postInput.trim()
    if (!text) return
    const { error: e } = await supabase.from("posts").insert([{ text }])
    if (e) return setError(e.message)
    setPostInput("")
    void loadBase()
  }

  const sendDM = async () => {
    const body = dmInput.trim()
    if (!body) return
    const receiver: Actor = sender === "me" ? "partner" : "me"
    const { error: e } = await supabase.from("direct_messages").insert([{ sender, receiver, body }])
    if (e) return setError(e.message)
    setDmInput("")
    void loadBase()
  }

  const toggleRecording = async () => {
    if (isRecording) {
      mediaRecorder.current?.stop()
      setIsRecording(false)
      return
    }
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true })
      mediaStream.current = stream
      mediaRecorder.current = new MediaRecorder(stream)
      audioChunks.current = []
      mediaRecorder.current.ondataavailable = (e) => audioChunks.current.push(e.data)
      mediaRecorder.current.onstop = async () => {
        try {
          const blob = new Blob(audioChunks.current, { type: "audio/webm" })
          const fileName = `voicemail-${Date.now()}.webm`
          const { error: uploadError } = await supabase.storage.from("voicemails").upload(fileName, blob)
          if (uploadError) return setError(uploadError.message)
          const { data } = supabase.storage.from("voicemails").getPublicUrl(fileName)
          const { error: insertError } = await supabase.from("voicemails").insert([{ url: data.publicUrl }])
          if (insertError) return setError(insertError.message)
          void loadBase()
        } finally {
          mediaStream.current?.getTracks().forEach((t) => t.stop())
          mediaStream.current = null
          setIsRecording(false)
        }
      }
      mediaRecorder.current.start()
      setIsRecording(true)
    } catch {
      setError("Microphone access failed.")
    }
  }

  const createAlbum = async () => {
    const name = newAlbumName.trim()
    if (!name) return
    const { error: e } = await supabase.from("albums").insert([{ name }])
    if (e) return setError(e.message)
    setNewAlbumName("")
    void loadBase()
  }

  const renameAlbum = async (albumId: string) => {
    const name = (renameDrafts[albumId] ?? "").trim()
    if (!name) return
    const { error: e } = await supabase.from("albums").update({ name }).eq("id", albumId)
    if (e) return setError(e.message)
    void loadBase()
  }

  const uploadToAlbum = async (albumId: string, fileList: FileList | null) => {
    if (!fileList?.length) return
    setUploadingAlbumId(albumId)
    const rows: Array<{ album_id: string; url: string }> = []
    for (const file of Array.from(fileList)) {
      const ext = (file.name.split(".").pop() ?? "jpg").replace(/[^a-zA-Z0-9]/g, "")
      const path = `${albumId}/${Date.now()}-${crypto.randomUUID()}.${ext || "jpg"}`
      const { error: uploadError } = await supabase.storage.from("gallery").upload(path, file)
      if (uploadError) {
        setError(uploadError.message)
        continue
      }
      const { data } = supabase.storage.from("gallery").getPublicUrl(path)
      rows.push({ album_id: albumId, url: data.publicUrl })
    }
    if (rows.length) {
      const { error: e } = await supabase.from("album_images").insert(rows)
      if (e) setError(e.message)
    }
    await loadImages(albumId)
    setUploadingAlbumId(null)
  }

  return (
    <main className="min-h-screen bg-[#f3d0d7] text-[#8a5d68] relative">
      <button className="fixed top-5 left-5 z-50 bg-white/70 rounded-full border border-white p-3" onClick={() => setIsMenuOpen(true)}>☰</button>
      <div className={`fixed inset-0 bg-black/10 z-40 transition ${isMenuOpen ? "opacity-100" : "opacity-0 pointer-events-none"}`} onClick={() => setIsMenuOpen(false)} />
      <aside className={`fixed left-0 top-0 h-full w-[340px] max-w-[88vw] bg-white/90 border-r border-white p-6 z-50 transition-transform ${isMenuOpen ? "translate-x-0" : "-translate-x-full"}`}>
        <div className="space-y-3">
          {(["Home", "Messages", "Voicemails", "Gallery"] as View[]).map((item) => (
            <button key={item} onClick={() => { setView(item); setIsMenuOpen(false) }} className={`block w-full text-left p-4 rounded-3xl border ${view === item ? "bg-white border-white shadow" : "bg-white/40 border-white/70"}`}>{item}</button>
          ))}
        </div>
        <div className="mt-8 space-y-3">
          <StatusCard label="Me" id={MY_ID} user={myStatus} />
          <StatusCard label="Partner" id={PARTNER_ID} user={partnerStatus} />
        </div>
      </aside>

      <div className="max-w-4xl mx-auto p-6 pt-24 space-y-6">
        {error && <div className="bg-red-50 border border-red-200 text-red-700 p-3 rounded-2xl text-sm font-bold">{error}</div>}

        {view === "Home" && (
          <section className="space-y-6">
            <div className="p-10 rounded-[3rem] bg-white/70 border border-white text-center">
              <p className="text-4xl sm:text-6xl font-black italic text-[#cc8292]">{timeSince}</p>
            </div>
            <div className="p-6 rounded-[2.5rem] bg-white/70 border border-white">
              <textarea rows={2} value={postInput} onChange={(e) => setPostInput(e.target.value)} className="w-full bg-transparent text-xl font-bold outline-none resize-none" placeholder="Recent thoughts..." />
              <div className="mt-4 text-right"><button onClick={() => void sendPost()} className="bg-[#cc8292] text-white px-6 py-3 rounded-full font-black text-xs tracking-[0.2em] uppercase">Send Post</button></div>
            </div>
            {posts.map((post) => <div key={post.id} className="p-6 rounded-[2.5rem] bg-white/60 border border-white"><p className="text-xl font-bold">{post.text}</p></div>)}
          </section>
        )}

        {view === "Messages" && (
          <section className="space-y-6">
            <div className="p-6 rounded-[2.5rem] bg-white/70 border border-white">
              <div className="inline-flex bg-white/70 rounded-full p-1 border border-white mb-4">
                <button onClick={() => setSender("me")} className={`px-4 py-2 rounded-full text-xs font-black uppercase ${sender === "me" ? "bg-[#cc8292] text-white" : "text-[#cc8292]"}`}>Me</button>
                <button onClick={() => setSender("partner")} className={`px-4 py-2 rounded-full text-xs font-black uppercase ${sender === "partner" ? "bg-[#cc8292] text-white" : "text-[#cc8292]"}`}>Partner</button>
              </div>
              <textarea rows={2} value={dmInput} onChange={(e) => setDmInput(e.target.value)} className="w-full bg-transparent text-xl font-bold outline-none resize-none" placeholder="Write a message..." />
              <div className="mt-4 text-right"><button onClick={() => void sendDM()} className="bg-[#cc8292] text-white px-6 py-3 rounded-full font-black text-xs tracking-[0.2em] uppercase">Send Message</button></div>
            </div>
            <div className="space-y-3">
              {directMessages.map((m) => (
                <div key={m.id} className={`max-w-[85%] ${m.sender === "me" ? "ml-auto" : ""}`}>
                  <div className={`p-4 rounded-3xl ${m.sender === "me" ? "bg-[#cc8292] text-white rounded-br-md" : "bg-white/70 border border-white rounded-bl-md"}`}>
                    <p className="font-bold text-lg">{m.body}</p>
                    <p className={`text-[10px] mt-2 uppercase tracking-widest ${m.sender === "me" ? "text-white/70" : "text-[#cc8292]/60"}`}>{m.sender} • {new Date(m.created_at).toLocaleString()}</p>
                  </div>
                </div>
              ))}
            </div>
          </section>
        )}

        {view === "Voicemails" && (
          <section className="space-y-6">
            <div className="p-8 rounded-[2.8rem] bg-white/70 border border-white text-center">
              <button onClick={toggleRecording} className={`w-20 h-20 rounded-full border-4 ${isRecording ? "bg-red-500 border-red-200 animate-pulse" : "bg-[#cc8292] border-white"}`} />
              <p className="mt-4 text-xs font-black uppercase tracking-[0.2em] text-[#cc8292]/60">{isRecording ? "Click to finish" : "Click to record note"}</p>
            </div>
            {voicemails.map((v, i) => (
              <div key={v.id} className="p-4 rounded-3xl bg-white/60 border border-white flex items-center justify-between gap-3">
                <div>
                  <p className="text-xs font-black uppercase text-[#cc8292]">Note {voicemails.length - i}</p>
                  <p className="text-[10px] opacity-50">{new Date(v.created_at).toLocaleString()}</p>
                </div>
                <audio src={v.url} controls className="h-8" />
              </div>
            ))}
          </section>
        )}

        {view === "Gallery" && (
          <section className="space-y-6">
            <div className="p-6 rounded-[2.5rem] bg-white/70 border border-white">
              <div className="flex flex-col sm:flex-row gap-2">
                <input value={newAlbumName} onChange={(e) => setNewAlbumName(e.target.value)} className="flex-1 rounded-full px-4 py-3 border border-white bg-white/70 outline-none font-bold" placeholder="New album name" />
                <button onClick={() => void createAlbum()} className="bg-[#cc8292] text-white px-6 py-3 rounded-full font-black text-xs tracking-[0.2em] uppercase">Create Album</button>
              </div>
            </div>
            <div className="space-y-3">
              {albums.map((album) => (
                <div key={album.id} className={`p-4 rounded-3xl border ${selectedAlbumId === album.id ? "bg-white border-white shadow" : "bg-white/50 border-white/70"}`}>
                  <div className="flex flex-wrap gap-2 items-center mb-2">
                    <button onClick={() => setSelectedAlbumId(album.id)} className="px-3 py-2 rounded-full bg-white border border-white text-[#cc8292] text-[10px] uppercase tracking-[0.2em] font-black">Open</button>
                    <span className="text-xs uppercase tracking-[0.2em] font-black text-[#cc8292]">{album.name}</span>
                  </div>
                  <div className="flex flex-col sm:flex-row gap-2">
                    <input value={renameDrafts[album.id] ?? album.name} onChange={(e) => setRenameDrafts((prev) => ({ ...prev, [album.id]: e.target.value }))} className="flex-1 rounded-full px-4 py-2 border border-white bg-white/70 outline-none text-sm font-bold" />
                    <button onClick={() => void renameAlbum(album.id)} className="px-4 py-2 rounded-full bg-white border border-white text-[#cc8292] text-[10px] uppercase tracking-[0.2em] font-black">Save Name</button>
                    <label htmlFor={`upload-${album.id}`} className={`px-4 py-2 rounded-full text-center text-[10px] uppercase tracking-[0.2em] font-black border border-white cursor-pointer ${uploadingAlbumId === album.id ? "bg-gray-200 text-gray-600" : "bg-[#cc8292] text-white"}`}>{uploadingAlbumId === album.id ? "Uploading..." : "Upload Images"}</label>
                    <input id={`upload-${album.id}`} type="file" multiple accept="image/*" className="hidden" onChange={(e) => { void uploadToAlbum(album.id, e.target.files); e.target.value = "" }} />
                  </div>
                </div>
              ))}
            </div>
            <div className="p-5 rounded-[2.5rem] bg-white/60 border border-white">
              {!albumImages.length ? (
                <p className="text-sm font-black uppercase tracking-[0.2em] text-[#cc8292]/60 text-center py-8">No images yet</p>
              ) : (
                <div className="grid grid-cols-2 sm:grid-cols-3 gap-3">
                  {albumImages.map((img) => (
                    <a key={img.id} href={img.url} target="_blank" rel="noreferrer" className="rounded-2xl overflow-hidden border border-white bg-white block">
                      <img src={img.url} alt="Album image" className="w-full h-36 object-cover" />
                    </a>
                  ))}
                </div>
              )}
            </div>
          </section>
        )}
      </div>
    </main>
  )
}
